.step1 {
  display: grid;
  grid-template:
    "h h h h h h" 40px
    "d d d m m m" 50vh
    "d d d m m m" 13vh
    "s s s s p p" 35vh;
  opacity: 0;
  transition: opacity 1s ease, background-color 3s ease, box-shadow 3s ease, border 3s ease, border-image 3s ease, filter 3s ease;
  grid-template-columns: 0.4fr 0.4fr 0.4fr 0.36fr 1fr 1fr;
  height: 100vh;
  &.visible {
    opacity: 1;
  }
}



.mainHeader {
  grid-area: h;
  background: var(--alabaster);
  display: block;
  position: relative;
  text-align: left;
  font-weight: bold;
  transition: background-color 3s ease, box-shadow 3s ease, border 3s ease, border-image 3s ease, filter 3s ease;
}
.header {
  font-size: 1.4em;
  color: var(--white-smoke);
  text-shadow: 1px 1px 2px var(--cornell-red);
  position: relative;
  &::after {
    content: "//";
    position: absolute;
    display: inline-block;
    top: -3px;
    right: 0.5em;
    font-size: 1.4em;
    color: var(--white-smoke);
    text-shadow: none;
  }
}

.helpButton {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background-color: var(--cinnabar);
  color: white;
  border: none;
  font-size: 16px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.2s, background-color 0.2s, background-color 3s ease, box-shadow 3s ease, border 3s ease, border-image 3s ease, filter 3s ease;
  
  &:hover {
    transform: translateY(-50%) scale(1.1);
    background-color: darken(#e8514c, 10%);
  }
  
  &:active {
    transform: translateY(-50%) scale(0.95);
  }
}

.tooltipContainer {
  position: relative;
  display: inline-block;
  margin-left: 5px;
}

.infoIcon {
  cursor: pointer;
  color: var(--cinnabar);
  font-size: 14px;
}

.tooltip {
  visibility: hidden;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 8px 12px;
  border-radius: 4px;
  width: 250px;
  font-size: 12px;
  line-height: 1.4;
  z-index: 100;
  top: calc(100% + 5px);
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s;
  text-align: left;
  
  &::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color:  transparent  transparent rgba(0, 0, 0, 0.8) transparent;
  }
}

.tooltipContainer:hover .tooltip {
  visibility: visible;
  opacity: 1;
}

.development {
  grid-area: d;
  background: var(--melon);
  box-shadow: inset 0px 0px 3px 9px var(--alabaster), inset 0px 0px 0px 10px var(--cinnabar);
  padding: 10px;
  overflow: hidden;
  transition: background-color 3s ease, box-shadow 3s ease, border 3s ease, border-image 3s ease, filter 3s ease;
}

.click {
  grid-area: l;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.mapContainer {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 200px;
  grid-area: m;
  overflow-y: scroll;
  border-radius: 10px;
  background: white;
  transition: background-color 3s ease, box-shadow 3s ease, border 3s ease, border-image 3s ease, filter 3s ease;
  
  :global(.map) {
    transition: transform 0.1s ease;
    
    &.clicking {
      transform: scale(0.95) !important;
    }
  }
}

.projects {
  grid-area: p;
  background: var(--melon);
  box-shadow: inset 0px 0px 3px 9px var(--alabaster), inset 0px 0px 0px 10px var(--cinnabar);
  padding: 10px;
  max-height: 35vh;
  overflow: hidden;
  transition: background-color 3s ease, box-shadow 3s ease, border 3s ease, border-image 3s ease, filter 3s ease;
}

.upgrades {
  grid-area: s;
  background: var(--melon);
  box-shadow: inset 0px 0px 3px 9px var(--alabaster), inset 0px 0px 0px 10px var(--cinnabar);
  display: flex;
  padding: 10px;
  flex-direction: column;
  overflow: hidden;
  transition: background-color 3s ease, box-shadow 3s ease, border 3s ease, border-image 3s ease, filter 3s ease;
}

.shop {
  max-height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  
  button {
    &:disabled {
      opacity: 50%;
    }
    color: white;
    border: 0;
    min-height: 50px;
    transition: background-color 3s ease, box-shadow 3s ease, border 3s ease, border-image 3s ease, filter 3s ease;
  }
}

.particlesCanvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.coinsContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 2;
}

.wireframe {
  background-color: white;
  box-shadow: none;
  border: 10px solid transparent;
  border-image: url('../../assets/marker/border.png') 57 57 57 57 round round;
  filter: grayscale(100%) contrast(1.5);
  transition: background-color 3s ease, box-shadow 3s ease, border 3s ease, border-image 3s ease, filter 3s ease;
}