.devTools {
  display: flex;
  gap: 10px;
  margin: 10px;
  position: fixed;
  top: 40px;
  right: 10px;
  z-index: 1000;
}

.devTools button {
  padding: 8px 12px;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color: #f0f0f0;
  cursor: pointer;
  
  &:hover {
    background-color: #e0e0e0;
  }
  
  &.active {
    background-color: #4CAF50;
    color: white;
    
    &:hover {
      background-color: #45a049;
    }
  }
}

.analyticsContainer {
  position: fixed;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 999;
  width: 90%;
  max-width: 1200px;
  max-height: 80vh;
  overflow-y: auto;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  background-color: white;
}
