.step0 {
  display: grid;
  grid-template:
    "h h h" 25px
    "m m m" auto;
  transition: padding-left 1s ease;
  background: white;
  
}
.selected {
  padding-left: 35%;
}

.header {
  grid-area: h;
  background: white url('../../assets/pre/Border=Striped2.png') repeat-x center center;
  background-size: 344px;
  color: white;
  text-shadow: 1px 1px 2px black,
  -1px -1px 2px black,
  1px -1px 2px black,
  -1px 1px 2px black;
  font-weight: bold;
  span {
    display: inline-block;
    background: white;
    margin: 3px 0;
    padding: 0 100px;
  }
}

.mapContainer {
  grid-area: m;
  position: relative;
  width: 100%;
  height: 100%;
  
  :global(.map) {
    width: 100%;
    height: 100%;
  }
} 